@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/*Some Resetting*/

*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}

body{
    /* background-color: #2ecc71; */
}

textarea:focus, input:focus{
    outline: none;
}


#search-box{
    padding: 30px;
    text-align: center;
}

#search-box input[type='text']{
    background-color: #eee;
    padding:12px;
    border: 1px solid #eee;
    margin: 10px;
    /* border-radius: 10px; */
}

#search-box button{
    background-color: #3498db;
    color:#fff;
    font-size: 1em;
    border: 1px solid #eee;
    padding: 10px;
    cursor: pointer;
    margin: 10px;
}

#today-weather{
    box-shadow: 7px 7px 3px #333;
    background:  -webkit-gradient(linear, left top, right bottom, from(#777777d0), to(#77777749)),
    url(/thundersky/static/media/evening.d03f4559.png) no-repeat;
    background:  -webkit-linear-gradient(left top, #777777d0, #77777749),
    url(/thundersky/static/media/evening.d03f4559.png) no-repeat;
    background:  linear-gradient(to right bottom, #777777d0, #77777749),
    url(/thundersky/static/media/evening.d03f4559.png) no-repeat;
    background-size:cover;
    font-family: 'Roboto', sans-serif;
    /* height: 60vh; */
    margin: 2%;
    padding: 20px;
    border-radius: 15px;
    color:#fff;
    background-color: #364760;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
}

.today-detail h2{
    padding: 10%;
    color: #fff;
    margin: 10px auto;
    font-size: 5em;
}

.today-detail p{
    font-size: 2em;
    color:#fff;
}

.today-detail{
    width: 50%;
}

.detail{
    margin-top: 30px;
    width: 100%;
}

.detail h3{
    display: inline;
    padding: 10px;
    margin-left: 20px;
    /* font-size: 1.5em; */
}

.today-detail img{
    height: 300px;
    width: 300px;
}

#forecast{
    background-color: rgb(225, 226, 226);
}

#forecast .forecast-day{
    /* background-color: #2ecc71; */
    background-color: #34495e;
    box-shadow: 7px 7px 3px #333;
    color: #fff;
    width: 30%;
    margin: 1.5%;
    border-radius: 10px;
    float: left;
    padding: 20px;
    text-align: center;
}

#forecast .forecast-day h2{
    padding: 10px;
}

@media only screen and (max-width: 900px){
    .detail h3{
        display: block;
    }
}

@media only screen and (max-width: 700px) {
    #today-weather{
        height: auto;
    }

    .today-detail h2{
        padding: 0;
    }

    .today-detail p{
        padding-bottom: 30px;;
    }

    .today-detail img{
        height: 200px;
        width: 200px;
    }
    
    .today-detail{
        margin-bottom: 20px;
        width: 100%;
        float: none;
    }
  } 
#load-wrapper {
    position: fixed;
    background: #2A2438;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }	
  
  #loader{
    display: block;
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform:scale(4,4);
            transform:scale(4,4);
  }
  
  .ball,
  .colorRing,
  .ringTwo,
  .ringThree,
  .ringFour {
    top:50%;
    left:50%;
    display:block;
    border-radius:50%;
    position:absolute;
    vertical-align:middle;
  }
  
  .ball {
    width:10px;
    height:10px;
    margin:-6px 0 0 -6px;
    background: #5C5470;
    animation:2000ms pulseInPrimary 1500ms cubic-bezier(0.4, 0.0, 0.2, 1)  infinite;
    -webkit-animation:2000ms pulseInPrimary 1500ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite;	
  }
  
  .colorRing {
    width:16px;
    height:16px;
    margin:-9px 0 0 -9px;
    border:1px solid #86CB92 ;
    animation:2000ms pulseIn cubic-bezier(0.4, 0.0, 0.2, 1) infinite;
    -webkit-animation:2000ms pulseIn 100ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite;						
  }
  
  @keyframes pulseIn {
    0%{
      opacity:0;
      -webkit-transform:scale(0.2,0.2);
              transform:scale(0.2,0.2);
    }
    50% {
      opacity:1;
      -webkit-transform:scale(1,1);
              transform:scale(1,1);
    }
    100% {
      opacity:0;
      -webkit-transform:scale(0.2,0.2);
              transform:scale(0.2,0.2);
    }
  }
  
  @-webkit-keyframes pulseIn {
    0% {
      opacity:0;
      -webkit-transform:scale(0.2,0.2);
    }
    50% {
      opacity:1;
      -webkit-transform:scale(1,1);
    }
    100%{
      opacity:0;
      -webkit-transform:scale(0.2,0.2);
    }
  }
  
  @keyframes pulseInPrimary{
    0%{
      opacity:1;
      -webkit-transform:scale(0.2,0.2);
              transform:scale(0.2,0.2);
    }
    50%{
      opacity:1;
      -webkit-transform:scale(1.4,1.4);
              transform:scale(1.4,1.4);
    }
    100%{
      opacity:1;
      -webkit-transform:scale(0.1,0.1);
              transform:scale(0.1,0.1);
    }
  }
  
  @-webkit-keyframes pulseInPrimary{
    0%{
      opacity:0;
      -webkit-transform:scale(0.1,0.1);
    }
    50%{
      opacity:1;
      -webkit-transform:scale(1,1);
    }
    100%{
      opacity:0;
      -webkit-transform:scale(0.2,0.2);
    }
  }
  
  
  .ringTwo{
    width:40px;
    height:40px;
    background: rgba(250, 250, 250, 0.03);
    border:1px solid #fafafa;
    margin:-21px 0 0 -21px;
    animation:2000ms pulseIn 500ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite;
    -webkit-animation:2000ms pulseIn 500ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite	
  }
  
  @keyframes pulseInSecondary {
    0%{
      opacity:0;
      -webkit-transform:scale(0.2,0.2);
              transform:scale(0.2,0.2);
    }
    50% {
      opacity:1;
      -webkit-transform:scale(1,1);
              transform:scale(1,1);
    }
    100% {
      opacity:0;
      -webkit-transform:scale(0.2,0.2);
              transform:scale(0.2,0.2);
    }
  }
  
  @-webkit-keyframes pulseInSecondary {
    0% {
      opacity:0;
      -webkit-transform:scale(0.2,0.2);
    }
    50% {
      opacity:1;
      -webkit-transform:scale(1,1);
    }
    100%{
      opacity:0;
      -webkit-transform:scale(0.2,0.2);
    }
  }
  
  
  .ringThree{
    width:48px;
    height:48px;
    border:1px solid rgba(250, 250, 250, 0.03);
    margin:-25px 0 0 -25px;
    animation:2000ms pulseInSecondary 550ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite;
    -webkit-animation:2000ms pulseInSecondary 550ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite;	
  }
  
  .ringFour{
    width:56px;
    height:56px;
    border:1px solid rgba(250, 250, 250, 0.03);
    margin:-29px 0 0 -29px;
    animation:2000ms pulseInSecondary 600ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite;
    -webkit-animation:2000ms pulseInSecondary 600ms cubic-bezier(0.4, 0.0, 0.2, 1) infinite;	
  }
  
  #label {
    position: relative;
    top: 66%;
  }
  
  p {
    color: #d0cade;
    font-size: .889em;
    line-height: 1.2;
    font-family: 'Inconsolata', sans-serif;
    text-align: center;
  
  }
  h2 {
    color: #fafafa;
    font-size: 1.777em;
    line-height: 1.2;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
  }
  
  
  
